import {useState, useEffect} from "react";
import css from './standingprescription.module.scss';


const StandingPrescription = () =>{
const [data, setData] = useState("");
useEffect( ()=>{
fetchData();

}, [])


const fetchData =async ()=>{
  const res = await fetch("http://localhost:4000/standingprescriptions/1", {
      method:"GET",
      headers: {
        'Content-Type': 'application/json'
      },
  });
  const data = await res.json();
  console.log(data);
  setData(data);
}

if(data.data){
 return(
    <div className={css.standingprescription}>
        <ul>
          {data.data.map((item)=>{
              return <li key={item.prescription_id}><b className={css.prescription}>{item.d_id}</b> <b className={css.prescription_date}>{item.date_created.split("T")}</b></li>
          })}
        </ul>
    </div>
)   
}
return <div className={css.standingprescription}>{data.message}</div>;

}

export default StandingPrescription;